const actions = {
  getAdjustmentTypes: (page, search, filter) => ({
    type: 'GET_ADJUSTMENT_TYPES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createAdjustmentType: () => ({
    type: 'CREATE_ADJUSTMENT_TYPE_REQUEST'
  }),
  storeAdjustmentType: data => ({
    type: 'STORE_ADJUSTMENT_TYPE_REQUEST',
    payload: { body: data }
  }),
  editAdjustmentType: id_adjustment_type => ({
    type: 'EDIT_ADJUSTMENT_TYPE_REQUEST',
    payload: { id_adjustment_type: id_adjustment_type }
  }),
  updateAdjustmentType: (id_adjustment_type, data) => ({
    type: 'UPDATE_ADJUSTMENT_TYPE_REQUEST',
    payload: {
      id_adjustment_type: id_adjustment_type,
      body: data
    }
  }),
  showAdjustmentType: id_adjustment_type => ({
    type: 'SHOW_ADJUSTMENT_TYPE_REQUEST',
    payload: { id_adjustment_type: id_adjustment_type }
  }),
  deactivateAdjustmentType: id_adjustment_type => ({
    type: 'DEACTIVATE_ADJUSTMENT_TYPE_REQUEST',
    payload: { id_adjustment_type }
  }),
  activateAdjustmentType: id_adjustment_type => ({
    type: 'ACTIVATE_ADJUSTMENT_TYPE_REQUEST',
    payload: { id_adjustment_type }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
