import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import AdjustmentTypeUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/adjustmentType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/adjustment-types";


class UpdateAdjustmentType extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editAdjustmentType } = this.props;
    const { id } = this.props.match.params;
    editAdjustmentType(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.adjustmentType.error) {
      notification("error", this.props.adjustmentType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.adjustmentType.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { updateAdjustmentType } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing location code
    //  value.location_code = value.location_code.toUpperCase();
    updateAdjustmentType(id, value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="adjustmentTypes.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.adjustmentType.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <AdjustmentTypeUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                adjustmentType={this.props.adjustmentType}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    adjustmentType: state.adjustmentType,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateAdjustmentType);
